/* global jQuery:false */

// put form into sentence format
(function ($) {
	"use strict";

	$(document).on("cleanMarketoHTMLAndCSSComplete", function () {
		// add text between form fields
		$(".field").each(function (index, element) {
			var fieldId = $(element).attr("data-fieldId");
			var text = $(element).attr("data-text");
			$(".mktoFormRow").each(function (index, element) {
				if ($(element).hasClass(fieldId)) {
					$(element).before("<span class='text " + fieldId.toLowerCase().replace(" ", "") + "'>" + text + "</span>");
				}
			});
		});

		$(".mktoFormRow.Email").after("<span class='text'>.</span>");

		// lowercase field placeholder names
		$(".q-form input").each(function (index, element) {
			var placeholder = $(element).attr("placeholder");

			if (placeholder !== undefined) {
				$(element).attr("placeholder", placeholder.toLowerCase());
			}
		});
	});
}(jQuery));

// dynamically resize text fields based on value length
(function ($) {
	"use strict";

	$(document).on("cleanMarketoHTMLAndCSSComplete", function () {
		$(document).on("blur", "input[type='text'], input[type=email]", function () {
			var value = $(this).val();

			if (value !== undefined) {
				var numChars = value.length;

				if (numChars > 0) {
					$(this).css("width", numChars+"em");
				}
			}
		});
	});
}(jQuery));